//CONSTANTSD TO ADJUST
const MIN_SPEED = 0.1;
const MAX_SPEED = 1;

const ANIMATION_GAP = 5;

const HOVER_TIME = 1500;

const ENLARGE_SPEED = 1;

//HELPER FUNCTIONS
function makePos(num) {
    return num < 0 ? num * -1 : num;
}

function randomSpeed() {
    return Math.random() * (MAX_SPEED - MIN_SPEED) + MIN_SPEED;
}

function randomDirection() {
    return Math.random() > 0.5 ? 1 : -1;
}

//OBJECT DECLARATION
class Page {
    image;
    floaties;
    x;
    y;
    width;
    StartWidth
    height;
    StartHeight;
    xSpeed;
    ySpeed;
    focus;
  
    constructor(isPage ,image, floaties, x, y, width, height, xSpeed, ySpeed) {
        this.image = image;
        this.floaties = floaties;
        this.x = x;
        this.y = y;
        this.width = width;
        this.StartWidth = width;
        this.height = height;
        this.StartHeight = height;
        this.xSpeed = xSpeed;
        this.ySpeed = ySpeed;
        this.focus = false;

        if(isPage){
            this.image.addEventListener("mouseenter", (event) => {this.handlerIn()});
            this.image.addEventListener("mouseleave", (event) => {this.handlerOut()});
        }


        this.moveImage();
    }

    getImage() { return this.image; }
    getX() { return this.x; }
    getY() { return this.y; }
    getWidth() { return this.width; }
    getHeight() { return this.height; }
    getXspeed() { return this.xSpeed; }
    getYspeed() { return this.ySpeed; }
    getFocus(){ return this.focus; }

    moveImage() {
        const container = document.querySelector('#animationCanvas');
        const canvasRect = container.getBoundingClientRect();

        this.height = this.image.getBoundingClientRect().height;

        // Collision detection with the container edges (borders)
        /*if(image.getBoundingClientRect().x< canvasRect.x+(3*xSpeed) && image.getBoundingClientRect().x >= canvasRect.x+(2*xSpeed)){
            xPos = 3* xSpeed;
        }   */ 

        //TODO do this for the container div not the screen
        if (this.image.getBoundingClientRect().x <= canvasRect.x) {
            this.xSpeed = makePos(this.xSpeed);
            this.x = 0;
        } else if (this.image.getBoundingClientRect().x + this.width >= canvasRect.right) {
            this.x = canvasRect.right - this.width - canvasRect.x;
            this.xSpeed = -makePos(this.xSpeed); // Reverse horizontal direction
        }
        this.x += this.xSpeed;

        /*if(image.getBoundingClientRect().y< canvasRect.y+(3*xSpeed) && image.getBoundingClientRect().y >= canvasRect.y+(2*xSpeed)){
            yPos = 3* ySpeed;
        }    */

        if (this.image.getBoundingClientRect().y <= canvasRect.y) {
            this.ySpeed = makePos(this.ySpeed);
            this.yPos = 0;
        } else if (this.image.getBoundingClientRect().y + this.height >= canvasRect.bottom) {
            this.yPos = canvasRect.bottom - this.height - canvasRect.y;
            this.ySpeed = -makePos(this.ySpeed); // Reverse vertical direction
        }
        this.y += this.ySpeed;

        this.floaties.forEach(otherImage => {
            if(this.image !== otherImage.getImage() && this.checkCollision(otherImage)){
                this.setNewDirection(otherImage);
            }
        });

        // Update the position of the image
        this.image.style.left = `${this.x}px`;
        this.image.style.top = `${this.y}px`;

        window.requestAnimationFrame(this.moveImage.bind(this));
    }

    checkCollision(otherImage){
        const otherRect = otherImage.getImage().getBoundingClientRect();
        const thisRect = this.image.getBoundingClientRect();
        if(thisRect.x<=(otherRect.x + otherImage.getWidth()) && thisRect.x + this.width>=otherRect.x){
            if(thisRect.y + this.height>=otherRect.y && thisRect.y<=otherRect.y + otherImage.getHeight()){
                return true;
            }
        }
        return false;
    }

    setNewDirection(otherImage){ 
        const thisXcenter = this.x + this.width / 2;
        const thisYcenter = this.y + this.height / 2;
        const otherXcenter = otherImage.getX() + otherImage.getWidth() / 2;
        const otherYcenter = otherImage.getY() + otherImage.getHeight() / 2;

        const directionX = otherXcenter - thisXcenter;
        const directionY = otherYcenter - thisYcenter;

        const distance = Math.sqrt(directionX * directionX + directionY * directionY);
        const normX = directionX / distance;
        const normY = directionY / distance;
        const cSpeed = Math.sqrt(this.xSpeed * this.xSpeed + this.ySpeed * this.ySpeed);

        this.xSpeed = cSpeed * -normX;
        this.ySpeed = cSpeed * -normY;

        const otherBox = otherImage.getImage().getBoundingClientRect();
        const thisBox = this.image.getBoundingClientRect();

        if(this.x > otherImage.getX()){
            //this is on the right
            if(this.y > otherImage.getY()){
                //this is below
                if(otherBox.bottom - thisBox.top < otherBox.right - thisBox.left){
                    //vertical jump
                    if(otherBox.bottom - thisBox.top > ANIMATION_GAP){
                        this.y += ANIMATION_GAP;//otherBox.bottom;
                    }
                }else{
                    //horizontal jump
                    if(otherBox.right - thisBox.left > ANIMATION_GAP){
                        this.x += ANIMATION_GAP;//otherBox.right;
                    }
                }
            }else{
                //this is above
                if(thisBox.bottom - otherBox.top < otherBox.right - thisBox.left){
                    //vertical jump
                    if(thisBox.bottom - otherBox.top > ANIMATION_GAP){
                        this.y -= ANIMATION_GAP;//otherBox.top - this.height;
                    }
                    
                }else{
                    //horizontal jump
                    if(otherBox.right - thisBox.left > ANIMATION_GAP){
                        this.x += ANIMATION_GAP;//otherBox.right;
                    }
                }
            }
        }else{
            //this is on the left
            if(this.y > otherImage.getY()){
                //this is below
                if(otherBox.bottom - thisBox.top < thisBox.right - otherBox.left){
                    //vertical jump
                    if(otherBox.bottom - thisBox.top > ANIMATION_GAP){
                        this.y += ANIMATION_GAP;//otherBox.bottom;
                    }
                }else{
                    //horizontal jump
                    if(thisBox.right - otherBox.left > ANIMATION_GAP){
                        this.x -= ANIMATION_GAP;//otherBox.left - this.width;
                    }
                }
            }else{
                //this is above
                if(thisBox.bottom - otherBox.top < thisBox.right - otherBox.left){
                    //vertical jump
                    if(thisBox.bottom - otherBox.top > ANIMATION_GAP){
                        this.y -= ANIMATION_GAP;//otherBox.bottom - this.height;
                    }
                }else{
                    //horizontal jump
                    if(thisBox.right - otherBox.left > ANIMATION_GAP){
                        this.x -= ANIMATION_GAP;//otherBox.left - this.width;
                    }
                }  
            }
        }
    }

    handlerIn(){
        if(!this.focus){
            this.timeoutId = setTimeout(() => this.setMain(), HOVER_TIME); 
        }
    }

    setMain(){
         //take focus away from previous thing
         this.floaties.forEach(floaty => {
            if (floaty.getFocus()){
                floaty.setNotMain();
            }
         })
        this.focus = true;

        this.achieveSize(this.StartWidth * 3, this.StartHeight * 3);
    }

    achieveSize(goalWidth, goalHeight){
    
        const animate = () => {
    
            if(this.height > goalHeight){
                this.height -= ENLARGE_SPEED;
            }else if(this.height < goalHeight){
                this.height += ENLARGE_SPEED;
            }

            if(this.width > goalWidth){
                this.width -= ENLARGE_SPEED;
            }else if(this.width < goalWidth){
                this.width += ENLARGE_SPEED;
            }
    
            // Set the image size dynamically
            this.image.style.width = `${this.width}px`;
            this.image.style.height = `${this.height}px`;
    
            if (makePos(goalHeight - this.height) >= ENLARGE_SPEED) {
                window.requestAnimationFrame(animate);
            } 
        };
    
        window.requestAnimationFrame(animate);
    }

    setNotMain(){
        this.focus = false;

        this.achieveSize(this.StartWidth, this.StartHeight);
    }

    handlerOut(){
        clearTimeout(this.timeoutId);
    }
}
  

//MAIN FUNCTIONS
function loadPages() {
    const images = document.querySelectorAll('.post-container');

    const bio = document.querySelector('#bio-box');
    const bioBox = bio.getBoundingClientRect();

    const container = document.querySelector('#animationCanvas');
    const canvasRect = container.getBoundingClientRect();

    let floaties = [];

    const bioFloat = new Page(false, bio, floaties, 10, 10, bioBox.width, bioBox.height, randomSpeed() * randomDirection(), randomSpeed() * randomDirection());
    floaties.push(bioFloat);

    let i = 0;
    let col = 0;
    images.forEach(image => {
        const START_WIDTH = Number(image.dataset.width);
        const START_HEIGHT = Number(image.dataset.height);

        var xPos = (i * (START_WIDTH + 50) + 50 + bioBox.right);
        i++;
        var yPos = col * START_HEIGHT;
        image.style.position = 'absolute';
        image.style.left = `${xPos}px`;
        image.style.top = `${yPos}px`;

        // Set the image size dynamically
        image.style.width = `${START_WIDTH}px`;
        image.style.height = `${START_HEIGHT}px`;

        if (canvasRect.x + xPos + (START_WIDTH * 2) > canvasRect.right) {
            col++;
            i = 0;
        }

        let xSpeed = randomSpeed() * randomDirection();
        let ySpeed = randomSpeed() * randomDirection();

        const page = new Page(true, image, floaties, xPos, yPos, START_WIDTH, START_HEIGHT, xSpeed, ySpeed);
        floaties.push(page);
    });
}

//LOAD
document.addEventListener("DOMContentLoaded", loadPages);